<template>
  <div class="succeed-container">
    <img src="../assets/images/succeed.png" alt="">
    <p class="title">{{successTitle}}</p>
    <div class="button-wrapper">
       <p  class="first-btn"  tag="div" @click="goFirst">{{firstBtnTitle}}</p>
       <p class="second-btn"   tag="div" @click="goSecond" v-if="secondBtnTitle">{{secondBtnTitle}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TheSuccess',
  props: {
    successTitle: {
      type: String,
      default: ''
    },
    firstBtnTitle: {
      type: String,
      default: ''
    },
    secondBtnTitle: {
      type: String,
      default: ''
    }
  },
  methods: {
    goFirst () {
      this.$emit('handleFirst')
    },
    goSecond () {
      this.$emit('handleSecond')
    }
  }
}
</script>

<style scoped lang="scss">
  @import "@/assets/styles/mixin.scss";
  .succeed-container{
    img{
      display: block;
      margin:0 auto;
      margin-top:90px;
      width: 72px;
      height: 72px;
    }
    .title{
      text-align: center;
      font-size: 16px;
      color: $color-text;
      margin:30px 0 72px;
    }
    .button-wrapper{
      width:334px;
      height:50px;
      line-height: 50px;
      font-size: 16px;
      border-radius: 2px;
      text-align: center;
      margin:0 auto;
      .first-btn{
        cursor: pointer;
        color: #fff;
        background-image: linear-gradient(-180deg, #25E5DE 0%, #1CD7D1 100%);
      }
      .second-btn{
        cursor: pointer;
        margin-top: 21px;
        border:$border-active-color;
        color: $color-active-text;
      }
    }
  }
</style>
